import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Loading  } from "carbon-components-react";

const dispatchedEvents = [];

/*
    Example: <srs-loader active="true" size="md" content="Descripton of what is getting loaded..."/>
*/
const SrsLoader  = function (props, ref) {
    return <Loading active={props.active} small={props.size} description={props.content}/>;
}

const styles = `
    
`
const props = [{
    name: "active",
    defaultValue: true,
    type: "boolean"
}, {
    name: "size",
    defaultValue: "md",
    type: "string"
}, {
    name: "content",
    defaultValue: "",
    type: "string"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsLoader), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };